/// <reference path="../../shared/MultiLevel.d.ts"/>
import React from 'react';
import * as go from 'gojs';
import { ReactDiagram } from 'gojs-react';
import { Card, Typography } from '@material-ui/core';

const ACTIVE = "#31d45f";
const INACTIVE = "#1F4963";

export default function (props: {
    data: MultiLevelUserData[]
}) {
    const users = props.data
        .sort((a: any, b: any) => a.nombre - b.nombre)
        .map(x => ({
            key: x.id
            , name: (x.esUsuarioNuevo ? "⭐ " : "")
                + x.nombre
                + " - " + (x.total_comisionable || 0).toCurrency()
            , parent: x.parent1 || undefined
            , background: x.total_comisionable > 0 ? ACTIVE : INACTIVE
        }));

    return (<ReactDiagram
        initDiagram={initDiagram}
        divClassName="diagram-component"
        nodeDataArray={users}
    />)
}

export function ExplanationCard() {
    return (<Card>
        <div>
            <div style={{ marginRight: 5, background: ACTIVE, minWidth: 13, minHeight: 13, display: "inline-block" }} />
            <Typography style={{ display: "inline-block" }}>Usuarios activos (que han hecho pedidos en este periodo)</Typography>
        </div>
        <div>
            <div style={{ marginRight: 5, background: INACTIVE, minWidth: 13, minHeight: 13, display: "inline-block" }} />
            <Typography style={{ display: "inline-block" }}>Usuarios inactivos (que no han hecho pedidos en este periodo)</Typography>
        </div>
        <div>
            <div style={{ marginRight: 5, minWidth: 13, minHeight: 13, display: "inline-block" }} >⭐</div>
            <Typography style={{ display: "inline-block" }}>Usuario Nuevo</Typography>
        </div>
    </Card>);
}


function initDiagram() {
    // Since 2.2 you can also author concise templates with method chaining instead of GraphObject.make
    // For details, see https://gojs.net/latest/intro/buildingObjects.html
    const $ = go.GraphObject.make;  // for conciseness in defining templates

    const myDiagram =
        $(go.Diagram,
            {
                initialAutoScale: go.Diagram.UniformToFill,
                // define the layout for the diagram
                layout: $(go.TreeLayout, { nodeSpacing: 5, layerSpacing: 30 }),
                model: new go.TreeModel({
                    isReadOnly: true
                })
            });

    // Define a simple node template consisting of text followed by an expand/collapse button
    myDiagram.nodeTemplate =
        $(go.Node, "Horizontal",
            //{ selectionChanged: nodeSelectionChanged },  // this event handler is defined below
            $(go.Panel, "Auto",
                $(go.Shape, { stroke: null }, new go.Binding("fill", "background")),
                $(go.TextBlock,
                    {
                        font: "bold 13px Helvetica, bold Arial, sans-serif",
                        stroke: "white", margin: 3
                    },
                    new go.Binding("text", "name"))
            ),
            $("TreeExpanderButton")
        );

    // Define a trivial link template with no arrowhead.
    myDiagram.linkTemplate = $(go.Link,
        { selectable: false },
        $(go.Shape));  // the link shape

    return myDiagram;
}
