import { Card, CardContent } from "@material-ui/core";
import React from "react";
import API from "../../../Services/API";
import { MonthToString } from "../../../Utils/Calcs";
import LeftRigthHeader from "../LeftRightHeader";
export default function <P extends { data: any[], current_cycle: Cycle }>(Component: React.ComponentType<P>) {
    return class CardWithCycleTravel extends React.Component<P & CardWithCycleTravelProps> {
        state = {
            data: [] as any[],
            current_cycle: null as Cycle,
            statId: null as keyof DashboardResult,
            loading: false
        }
        constructor(props) {
            super(props);
            this.state.statId = props.statId;
            this.state.current_cycle = props.current_cycle;
            this.state.data = props.data;
        }
        handleBack = () => {
            if (this.state.loading) return;
            this.setState({ loading: true });
            this.fetchData(this.state.current_cycle.index - 1);
        }
        fetchData(cycleIndex: number) {
            API.Stats({ stats: [this.state.statId, "current_cycle"], cycleIndex })
                .then(this.updateData).catch(this.showError);
        }
        handleNext = () => {
            if (this.state.loading) return;
            this.setState({ loading: true });
            this.fetchData(this.state.current_cycle.index + 1);
        }
        updateData = (results) => {
            const { current_cycle, ...remain } = results;
            this.setState({
                data: remain[this.state.statId] || [],
                current_cycle,
                loading: false
            });
        }
        showError = (ex) => {
            this.setState({ loading: false });
            alert(ex.message);
        }
        render() {

            var { reduceFn, statId, title, ...componentProps } = this.props;
            const { current_cycle, loading } = this.state;
            var subtitle = "";
            if (current_cycle) {
                let desde = new Date(current_cycle.start);
                let hasta = new Date(current_cycle.end);
                subtitle = `${desde.getDate()} ${MonthToString(desde.getMonth())} - ` +
                    `${hasta.getDate()} ${MonthToString(hasta.getMonth())}`;
            }
            var { data } = this.state;
            if (reduceFn) data = reduceFn(data);

            const ComponentProps = {
                ...componentProps
                , current_cycle
                , loading
                , data: reduceFn ? reduceFn(this.state.data) : this.state.data
            } as P;

            return (
                <Card className="dashboard-card" style={{ display: "flex", flexDirection: "column" }} >
                    <LeftRigthHeader
                        loading={loading}
                        onBackClick={(current_cycle && current_cycle.index) ? this.handleBack : null}
                        onNextClick={this.handleNext}
                        subtitle={subtitle}
                        title={title}
                    />
                    <CardContent style={{ flex: 1, overflow: "auto", padding: 0 }}>
                        <Component {...ComponentProps} />
                    </CardContent>
                </Card>);
        }
    }
}

interface CardWithCycleTravelProps {
    statId: string;
    current_cycle: Cycle;
    title: string;
    reduceFn?: (data: any[]) => any[];
}